    * {
        margin: 0;
        padding: 0;
        font-family: poppins;
        box-sizing: border-box;
    }
    
    .hero {
        margin: auto;
        box-sizing: border-box;
        font-family: Poppins;
        max-width: 100%;
        background-position: center;
        background-size: cover;
    }
    
    .navbar {
        display: flex;
        padding: 40px 150px;
    }
    
    .navbar .fa {
        display: none;
    }
    
    .logo img {
        width: 130px;
    }
    
    .link {
        flex: 1;
    }
    
    .link ul {
        margin-left: 50px;
        display: inline;
    }
    
    .link ul li {
        list-style: none;
        display: inline-block;
        padding: 8px 25px;
        font-family: Poppins;
    }
    
    .link ul a {
        color: grey;
        font-size: 15px;
        font-weight: bold;
        font-family: Poppins;
    }
    
    .link ul li:hover::after {
        width: 100%
    }
    
    .btn {
        float: right;
        cursor: pointer;
        transition: .6s;
    }
    
    .signupbtn {
        padding: 10px 10px;
        color: white !important;
        background-color: cyan;
        border: 1px solid cyan;
        border-radius: 20px;
    }
    
    .btn3 {
        color: white;
        margin-top: 20px;
        margin-left: 230px;
        padding: 10px 20px;
        border: 1px solid hsl(187, 83%, 37%);
        border-radius: 20px;
        background-color: hsl(187, 83%, 37%);
        font-weight: bold;
        cursor: pointer;
        position: relative;
        float: left;
    }
    
    .title {
        margin: 140px 130px;
        color: grey;
        align-items: center;
    }
    
    .title h1 {
        font-size: 15px;
        margin-bottom: 0px;
        margin-left: -600px;
    }
    
    .title h1 span {
        font-size: 50px;
        font-weight: bold;
        color: hsl(260, 8%, 14%);
    }
    
    .maincontent {
        text-align: center;
    }
    
    .photo img {
        height: 400px;
        float: right;
        margin-top: -220px;
        margin-right: -130px;
        float: right;
    }
    
    .srch {
        margin-top: -140px;
        align-items: center;
        box-sizing: border-box;
        margin-left: 140px;
        margin-right: 310px;
    }
    
    .form {
        background-image: url(../src/img/bg-shorten-desktop.svg);
        background-color: hsl(257, 27%, 26%);
        border-radius: 2px;
        display: flex;
        align-items: center;
        height: 80px;
        border-radius: 5px;
    }
    
    .form input {
        width: 70%;
        height: 30px;
        justify-content: center;
    }
    
    #query {
        margin-left: 40px;
        border-radius: 5px;
    }
    
    .form button {
        margin-left: 10px;
        height: 40%;
        color: white;
        background-color: hsl(180, 66%, 49%);
        padding: 3px 20px;
        border: hsl(180, 66%, 49%);
        border-radius: 5px;
        cursor: pointer;
    }
    
    .body {
        margin-top: 250px;
        width: 100%;
        padding: 100px;
        border: 1px solid rgb(220, 245, 245);
        background-color: rgb(220, 245, 245);
        margin-left: 1px;
    }
    
    .body h1 {
        margin-top: 60px;
        font-size: 20px;
        text-align: center;
        color: hsl(260, 8%, 14%);
        font-weight: bold;
    }
    
    .body h3 {
        font-size: 12px;
        color: gray;
        text-align: center;
        margin-top: 10px;
    }
    
    .section {
        margin-top: 50px;
        padding: 40px;
        box-sizing: border-box;
        /* display: flex;
        flex-direction: row; */
        justify-content: space-between;
    }
    
    .section h2 {
        font-size: 15px;
        margin-top: 20px;
        font-weight: bold;
    }
    
    .section p {
        font-size: 8px;
        margin-top: 10px;
        color: gray;
    }
    
    .staticscards {
        margin-top: 80px;
        margin-left: 120px;
    }
    
    .mid {
        border: 1px solid white;
        padding: 10px;
        background-color: white;
        box-sizing: border-box;
    }
    
    .mid1 {
        border: 1px solid white;
        margin-top: 20px;
        padding: 10px;
        background-color: white;
        box-sizing: border-box;
    }
    
    .mid2 {
        border: 1px solid white;
        margin-top: 40px;
        padding: 20px;
        background-color: white;
        box-sizing: border-box;
    }
    
    .mid img {
        background-color: hsl(260, 8%, 14%);
        margin-top: -40px;
        border: 1px solid hsl(260, 8%, 14%);
        border-radius: 30px;
        padding: 10px;
        height: 45px;
    }
    
    .mid1 img {
        background-color: hsl(260, 8%, 14%);
        margin-top: -60px;
        border: 1px solid hsl(260, 8%, 14%);
        border-radius: 30px;
        padding: 10px;
        height: 45px;
    }
    
    .mid2 img {
        background-color: hsl(260, 8%, 14%);
        margin-top: -40px;
        border: 1px solid hsl(260, 8%, 14%);
        border-radius: 30px;
        padding: 10px;
        height: 45px;
    }
    
    .side {
        box-sizing: border-box;
        border: 1px solid hsl(257, 27%, 26%);
        margin-top: 0px;
        width: 100%;
        margin-left: -1px;
        height: 240px;
        background-image: url(../src/img/bg-boost-desktop.svg);
        display: inline-block;
        background-color: hsl(257, 27%, 26%);
        background-repeat: no-repeat;
    }
    
    .sidebar button {
        font-size: 15px;
        width: 9%;
        font-weight: bold;
        margin-top: 40px;
        margin-left: 650px;
        padding: 10px 5px;
        color: white;
        background-color: hsl(180, 66%, 49%);
        border: 1px solid hsl(180, 66%, 49%);
        border-radius: 20px;
        cursor: pointer;
        transition: .6s;
    }
    
    .sidebar h1 {
        margin-top: 60px;
        margin-left: 570px;
        font-size: 30px;
        color: white;
    }
    
    .side1 {
        display: none;
    }
    
    .footerbar {
        margin-top: -1px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid hsl(260, 8%, 14%);
        background-color: hsl(260, 8%, 14%);
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    
    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    
    .logo1 h1 {
        font-weight: bold;
        color: white;
        margin-top: 50px;
        margin-left: 170px;
    }
    
    .ull1 h3 {
        color: white;
        float: right;
        list-style-type: none;
        font-size: 15px;
        margin-top: 60px;
        margin-left: 260px;
        margin-right: 50px;
    }
    
    .ull1 ul {
        list-style-type: none;
        margin-left: 260px;
        font-size: 10px;
        color: gray;
    }
    
    .ull2 h5 {
        color: white;
        list-style-type: none;
        font-size: 15px;
        margin-top: 60px;
        margin-left: -140px;
    }
    
    .ull1 ul li a {
        color: gray;
    }
    
    .ull2 ul {
        list-style-type: none;
        margin-left: -140px;
        font-size: 10px;
        color: gray;
    }
    
    .ull2 ul li a {
        color: gray;
    }
    
    .ull3 h6 {
        color: white;
        list-style-type: none;
        font-size: 15px;
        margin-top: 60px;
        margin-left: -150px;
    }
    
    .ull3 ul {
        list-style-type: none;
        margin-left: -150px;
        font-size: 10px;
    }
    
    .ull3 ul li a {
        color: gray;
    }
    
    .social-icon {
        margin-top: 60px;
        margin-right: 130px;
    }
    
    .social-icon img {
        margin: 8px 8px;
        cursor: pointer;
        transition: .6s;
    }
    
